strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
video,
audio,
section,
article,
nav,
footer,
header,
aside {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
